html {
	overflow-y: scroll;

	&.no-overflow {
		overflow: unset;
	}

	&.js-height {
		height: 100%;
	}
}

img {
	// width: 100%;
	height: auto;
	max-width: 100%;
	max-height: 100%;
}

.fixed {
	overflow: hidden;
	// width:calc(100% - 17px);

}

.swiper-container,
.swiper-container-three,
.swiper-container-two {
	margin: 0 auto;
	position: relative;
	overflow: hidden;
	list-style: none;
	padding: 0 !important;
	z-index: 1;
	width: 100%;
}

.popup {
	display: none;

	.response-popup {
		padding: 32px;
		width: 320px;

		&__title {

			font-size: 24px;
			font-weight: 500;
			font-style: normal;
			font-stretch: normal;
			line-height: 1;
			letter-spacing: normal;
			text-align: center;
			color: #2e2e2e;
			margin: 1rem 0;
		}

		&__text {

			font-size: 14px;
			font-weight: normal;
			font-style: normal;
			font-stretch: normal;
			line-height: 1.57;
			letter-spacing: normal;
			text-align: center;
			color: #262626;
			margin-bottom: 1rem;
		}

		&__inner {
			display: flex;
			flex-direction: column;
			align-items: center;
			width: 100%;
		}
	}
}

h1.title {
	color: #333333;
	font-family: "Roboto Condensed";
	font-size: 30px;
	font-weight: 400;
	line-height: 44px;
	text-transform: uppercase;
	width: 75%;
	margin-bottom: 20px;
	margin-top: 10px;
}

.container {
	.col-md-9 {
		padding: 0;
		position: relative;

		@media (min-width: 768px) {
			margin-left: 7px;
			flex: 0 0 calc(75% - 7px);
			max-width: calc(75% - 7px);
		}
	}

	&.new-catalog {
		position: relative;
		// width: calc(100% - 32px);
	}
}

.col-md-3 {
	padding-left: 0;

}

.fancybox-container {
	transition: all 0.3s ease-in-out !important;
}

.fancybox-image-wrap {
	top: 50% !important;
	transform: translateX(-50%)translateY(-50%)scale(1) !important;
	left: 50% !important;

	max-width: 100% !important;
	height: 98% !important;
	display: flex !important;
	flex-direction: column !important;
	align-items: center !important;
	justify-content: center !important;
	transition: all 0.3s ease-in-out;

	@media screen and (min-width: 768px) {
		width: auto !important;
	}

	img {
		position: unset !important;
		width: auto !important;
		height: auto !important;
		max-width: 100%;
		max-height: 100%;
		cursor: pointer !important;
	}


}

.fancybox-container {
	transition-duration: 500ms !important;
}

.compensate-for-scrollbar {
	margin-right: 0px !important;
}

body.fancybox-active {
	overflow: unset !important;
}

.docs-frame {
	// flex-shrink: 0;
	max-width: 100%;
	min-height: 400px;
	display: flex !important;
	flex-direction: column;
	align-items: center;
	// overflow-y: auto;
	margin-bottom: 40px;

	iframe {
		// height: auto !important;
		// height: 100% !important;
		height: 90vh !important;

		// height: -moz-available !important;
		// /* WebKit-based browsers will ignore this. */
		// height: -webkit-fill-available !important;
		// /* Mozilla-based browsers will ignore this. */
		// height: fill-available !important;
		// width: 100%;
		// // overflow: hidden;
		// margin: 0px;
		// // max-height: 1176px !important;
		// overflow-y: scroll !important;
		// height: 1176px !important;
		// padding-bottom: 24px;
		@media screen and (max-width: 767px) {
			height: 60vh !important;
		}
	}
}

.BadPols {
	border-color: #e03127 !important;
}

.button-close {
	position: absolute;
	top: 6px;
	right: 7px;
	background: url('../images/close.svg');
	background-size: 100%;
	width: 12px;
	height: 12px;
	cursor: pointer;
	z-index: 10;
}

.hide {
	display: none;
}

picture {
	width: 100%;
	text-align: center;
	max-width: 100%;
	max-height: 100%;
	// display: flex;
	// align-items: stretch;
	text-align: center;
	// justify-content: center;
	display: block;
}

@media screen and (max-width: 767px) {
	h1.title {
		font-size: 30px;
		font-weight: 500;
		font-style: normal;
		font-stretch: normal;
		line-height: 42px;
		letter-spacing: normal;
		text-align: left;
		color: #262626;
		width: 100%;
		background: none;

		&.shipping-payment__title {
			padding: 15px 0;
		}
	}


	.col-md-3 {
		padding-right: 0;
		padding-left: 0;
		padding-top: 14px;

		&.order {
			order: 1;
		}
	}

	.fixed {
		width: 100%;

		.header {
			+.section {
				opacity: 0;
			}
		}
	}

}
