.text-default,
.articles-main__info,
.about-store,
.description,
.about-brand {
	margin-bottom: 48px;
	display: flex;
	flex-direction: column;

	img {
		display: block;
		max-width: 100%;

		// margin-bottom: 28px;
		+p {
			margin-top: 28px;
		}
	}

	h2 {
		color: #333333;
		font-family: "Roboto Condensed";
		font-size: 24px;
		font-weight: 400;
		line-height: 24px;
		margin-bottom: 16px;
		margin-top: 0px;

		b {
			margin: 0;
		}

		&:first-child {
			width: 100%;
		}
	}

	h3 {
		color: #333333;
		font-family: "Roboto Condensed";
		font-size: 20px;
		font-weight: 400;
		line-height: 28px;

	}

	h4 {
		text-align: left;
		font-size: 22px;
		font-weight: 500;
		font-style: normal;
		font-stretch: normal;
		line-height: 32px;
		letter-spacing: normal;
		color: $base;
		margin-bottom: 40px;
	}

	h5 {
		color: #333333;
		font-family: "Roboto Condensed";
		font-size: 18px;
		font-weight: 400;
		line-height: 28px;

		b {
			color: $base;
			line-height: inherit;
		}
	}

	b {
		width: 100%;
		margin-bottom: 13px;
		color: #333333;
		font-family: "Roboto Condensed";
		font-size: 18px;
		font-weight: 700;
		line-height: 24px;
		// text-align: left;
		color: #333333;
		margin-top: 18px;
	}

	p {
		display: block;
		width: 100%;
		color: #333333;
		font-family: Arial, sans-serif;
		font-size: 14px;
		line-height: 22px;
		text-align: left;
		z-index: 99;
		margin: 6px 0 16px;

		a {
			color: $base;

			&[href^="tel:"] {
				font-size: 14px;
				font-weight: 500;
				font-style: normal;
				font-stretch: normal;
				line-height: 1.57;
				letter-spacing: normal;
				text-align: left;
				color: #262626;
			}

		}

		span {
			width: 100%;
			padding: 5px 0;
			font-family: Arial, sans-serif;
			font-weight: 700;
		}

		strong {
			margin-bottom: 0px;
		}

		&+strong {
			margin-bottom: 10px;
		}

		&+table {
			&+p {
				margin-top: 16px;
			}
		}

		// img {
		// 	margin: 0 auto;

		// 	&:nth-last-child(2):first-child,
		// 	&:nth-last-child(2):first-child~img {
		// 		color: green;
		// 	}

		// }
		img {
			margin-right: 0;
			margin-left: 0;
			margin-bottom: 34px;
		}
	}

	ol {
		color: #333333;
		font-family: Arial, sans-serif;
		font-size: 14px;
		font-weight: 700;
		line-height: 22px;
		padding-left: 0;
		margin-bottom: 20px;
	}

	ul {
		z-index: 99;
		list-style: disc;
		color: #333333;
		font-family: Arial,
			sans-serif;
		font-size: 14px;
		line-height: 22px;
		text-align: left;
		color: #333333;
		padding-left: 0;
		width: 100%;
		margin-bottom: 12px;
		margin-top: 0px;
		display: flex;
		flex-direction: column;

		p {
			margin-top: 12px;
		}

		// li:first-child {
		// 	margin-top: 16px;
		// }

		b {
			margin-bottom: 16px
		}
	}

	li {
		color: #333333;
		font-family: Arial, sans-serif;
		font-size: 14px;
		line-height: 20px;
		text-align: left;
		margin-left: 43px;
		font-weight: 400;
		margin-bottom: 8px;
	}

	strong {
		color: #333333;
		font-family: Arial, sans-serif;
		font-size: 14px;
		font-weight: 700;
		line-height: 22px;
	}

	blockquote {
		padding: 24px;
		background-color: #f8f8f8;
		margin-bottom: 2rem;

		b {

			width: 100%;
			margin-bottom: 13px;
			color: #333333;
			font-family: "Roboto Condensed";
			font-size: 18px;
			font-weight: 700;
			line-height: 24px;
			text-align: left;
			color: #333333;
			margin-top: 18px;
		}

		p {
			margin: 0 0 14px;

			b {
				font-size: inherit;
				line-height: inherit;
			}
		}

		strong {
			margin-bottom: 15px;
			display: block;
		}

		ul {
			li {
				color: #ba1212;
				margin-left: 16px;

				p {
					margin: 0;
				}
			}
		}

		&.column {
			display: flex;
			flex-wrap: wrap;

			ul {
				width: 33%;
			}

			.time {
				margin-top: 14px;

				br {
					display: none;
				}
			}
		}
	}


	tr {
		z-index: 99;
		list-style: none;
		display: flex;
		font-size: 16px;
		font-weight: 500;
		font-style: normal;
		font-stretch: normal;
		line-height: 1.5;
		letter-spacing: normal;
		text-align: left;
		color: #333333;
		padding-left: 0;
		width: 100%;
		border-bottom: 1px solid #e8e8e8;

		&:first-child {
			td {
				color: #666666;
				font-family: Arial, sans-serif;
				font-size: 12px;
				font-weight: 700;


			}
		}

		td:not(:first-child) {
			flex-grow: 1;
			min-width: 80px;
		}
	}

	td {
		font-family: Arial, sans-serif;
		color: #333333;
		font-size: 14px;
		display: flex;
		flex-direction: column;
		padding: 11px 0 11px 13px;

		&:first-child {
			width: 286px;
			flex-shrink: 0;
		}

		i {
			color: #666666;
			font-family: Arial, sans-serif;
			font-size: 14px;
			font-style: normal;
		}

		b {
			color: #666666;
			font-family: Arial, sans-serif;
			font-size: 12px;
			font-weight: 700;
			padding: 0;
			margin: 0;
		}
	}

	// div:not(.return-exchange__field) {
	// 	display: block;
	// 	// flex-wrap: wrap;
	// 	// justify-content: center;
	// }

	.table {
		width: 100%;
		margin-bottom: 22px;
		margin-top: 19px;

		tbody {
			.table_tr:nth-child(odd) {
				background-color: #f8f8f8;
			}

			.table_td {
				color: #333333;
				font-family: Arial, sans-serif;
				font-size: 14px;
				font-weight: 400;
				padding: 8px 0 7px 13px;

				&:first-child {
					width: unset;
					flex-grow: 1;
				}

				&:last-child {
					text-align: right;
					padding-right: 50px;
				}

			}
		}
	}

	hr {
		width: 100%;
		background: #e2e2e2;
		height: 2px;
	}

	blockquote {
		padding: 24px;
		background-color: #f8f8f8;
		margin-bottom: 2rem;

		b {
			display: inline;
			width: 100%;
			margin-bottom: 13px;
			color: #333333;
			font-family: "Roboto Condensed";
			font-size: 18px;
			font-weight: 700;
			line-height: 24px;
			text-align: left;
			color: #333333;
			margin-top: 18px;
		}
	}


	.maps {
		padding-top: 18px;
		border-bottom: 1px solid #e2e2e2;
		border-top: 1px solid #e2e2e2;
		margin-bottom: 30px;
	}

	.tags__status {
		width: 100%;
		height: auto;
		border: 1px solid #f1f1f1;
		display: flex;
		flex-direction: column;
		padding: 16px;

		a {
			color: $base;
			font-family: Arial, sans-serif;
			font-size: 12px;
			margin-right: 26px;
		}
	}

	.container {
		display: block;
	}


	&.seo-text {

		h2 {
			color: #262626;
			font-size: 26px;
			font-weight: 400;
			line-height: 24px;
		}
	}

	.official-dealers {
		display: flex;
		flex-wrap: wrap;
		width: 100%;
		margin-bottom: 29px;
		display: block;

		h2 {
			// text-align: center;
			width: 100%;
			margin-bottom: 18px;
			margin-top: 18px;
		}

		p {
			margin-bottom: 10px;

		}

		img {
			margin-bottom: 0;
		}

		// .official-dealers__link

		&__link {
			width: calc(50%-17px);
			display: flex;
			position: relative;
			overflow: hidden;
			margin-bottom: 19px;
			margin-top: 14px;
			display: inline-flex;

			&:nth-of-type(1) {
				margin-right: 29px;

			}

			&:after {
				content: '';
				background-image: url('../images/play.svg');
				position: absolute;
				background-repeat: no-repeat;
				background-size: 40px 40px;
				background-color: rgba(0, 0, 0, 0.5);
				background-position: center center;
				left: 0;
				top: 0;
				width: 100%;
				height: 100%;
			}

			&:hover {
				img {
					transform: scale(1.1);
				}
			}

			picture {
				min-height: 340px;
			}
		}
	}

	.about-store__button {
		display: none;
	}

	@media screen and (max-width: 767px) {
		margin-bottom: 30px;

		img {
			// max-height: 52px;
			margin-top: -5px;
			margin-bottom: 24px !important;
		}

		h3 {
			font-size: 18px;
			font-weight: 400;
			line-height: 24px;
			margin-bottom: 16px;
		}

		h4 {
			font-size: 16px;
			font-weight: 400;
			line-height: 25px;
			margin-bottom: 23px;
			padding-top: 0;
		}

		h5 {
			font-size: 16px;
			font-weight: 400;
			line-height: 24px;
		}

		p {
			font-size: 14px;
			line-height: 22px;

			margin-bottom: 24px;

			&+table+p {
				margin-top: 16px;
				margin-bottom: 35px;
			}


			&:last-child {
				margin-bottom: 0;
			}
		}

		b {
			// display: block;
			width: 100%;
			margin-bottom: 11px;
			font-size: 16px;
			font-weight: 400;
			line-height: 24px;
			margin-top: 0;
		}

		h2 {
			color: #333333;
			font-family: "Roboto Condensed";
			font-size: 22px;
			font-weight: 400;
			line-height: 30px;
		}

		ol {
			margin-bottom: 0;

			li {

				margin-left: 25px;
				line-height: 21px;
			}
		}

		ul {
			font-size: 16px;
			font-weight: 400;
			line-height: 24px;
			margin-bottom: 12px;
			letter-spacing: -0.15px;
		}

		li {
			color: #262626;
			font-size: 14px;
			margin-bottom: 4px;
			margin-left: 31px;
		}

		&.seo-text {

			h2 {
				color: #262626;
				font-size: 22px;
				font-weight: 400;
				line-height: 26px;
				margin-bottom: 26px;
			}

			img {
				padding: 0 !important;
				margin: 0 -15px;
				width: calc(100% + 30px);
				max-width: calc(100% + 30px);
			}
		}

		.official-dealers {
			&__link {
				width: calc(100%);
				display: flex;
				position: relative;
				overflow: hidden;
				margin-bottom: 5px;
				margin-top: 14px;

				&:nth-of-type(1) {
					margin-right: 0px;

				}

				.official-dealers__pic {
					margin: 0 0 0px !important;
					height: auto !important;
				}

				picture {
					min-height: 100%;
				}
			}
		}


		tr {

			&:first-child {
				td {}
			}
		}

		td {
			// width: 50% !important;

			&:first-child {
				width: 120px !important;
				padding-left: 0;
			}

			&:last-child {
				margin-left: auto;
			}

			i {}
		}

		.table {
			margin-top: 0;
			width: 100%;

			tbody {
				.table_tr:nth-child(odd) {}

				.table_tr {
					display: flex;
					flex-direction: column;
					padding: 9px 14px;
				}

				.table_td {
					font-family: Arial, sans-serif;
					font-size: 14px;
					font-weight: normal;
					font-style: normal;
					font-stretch: normal;
					letter-spacing: normal;
					color: #333333;
					width: unset;
					flex-shrink: 1;
					padding: 0;
					text-align: left;
					width: unset !important;


					&:first-child {
						margin-bottom: 10px;
					}

					&:last-child {
						flex-shrink: 0;
						text-align: left;
						margin-left: 0;
					}
				}
			}
		}

		blockquote {
			padding: 16px;
			margin-bottom: 35px;

			ol {
				margin-bottom: 10px;

				&+ol {
					margin-bottom: 0;
				}
			}


			a {
				margin-top: 22px;
			}

			&.column {
				ul {
					width: 100%;
					margin-bottom: 0;

					&:last-of-type {
						margin-bottom: 16px;
					}
				}

				.time {
					b {
						display: block;

					}

					br {
						display: block;
					}
				}
			}
		}

		.maps {
			margin-bottom: 20px;
		}

		.maps iframe {
			max-height: 260px;
		}

		.page__table {
			display: block;
			overflow-x: auto;

			tbody {
				display: flex;
				max-width: 1176px;
				overflow-x: scroll;
				width: 480px;
				flex-direction: column;
			}
		}

		&.seo-text {
			height: 700px;
			overflow: hidden;
			position: relative;
		}

		.about-store__button {
			position: absolute;
			bottom: 0px;
			left: 0;
			width: 100%;
			background: white;
			padding: 8px 15px;
			color: $base;
			font-family: Arial, sans-serif;
			font-size: 14px;
			display: flex;
			align-items: center;

			&:before {
				content: "";
				width: 119px;
				position: absolute;
				height: 1px;
				border-bottom: 1px dashed $base;
				bottom: 9px;
			}

			&:after {
				content: '';
				background: url("https://icongr.am/entypo/chevron-down.svg?color=666666") center/16px no-repeat;
				width: 16px;
				height: 16px;
				position: absolute;
				left: calc(145px);
			}
		}

		&.active {
			height: auto;

			.about-store__button {
				position: static;

				&:before {
					width: 63px;
				}

				&:after {
					content: '';
					background: url("https://icongr.am/entypo/chevron-up.svg?color=666666") center/16px no-repeat;
					left: 80px;
				}
			}

		}
	}
}
